import {SET_SEARCH_TERM} from '../actions/type';

export const initialState={
    term:null,
}

const reducer = (state={}, action)=>{
    
        console.log(action);

        switch(action.type){
            case SET_SEARCH_TERM:
                return{
                    ...state,
                    term:action.term,
                };

            default:
                return state;
            }
        }
export default reducer;